<template>
              <div class="verification-wrap">
                    <el-button type="text" @click="verificationButton"
                      ></el-button
                    >
                    <el-dialog
                      title=""
                      :visible.sync="dialogbuttonVisible"
                      width="30%"
                    >
                      <div class="verification-slide">
                        <slide-verify
                          :l="40"
                          :r="10"
                          :w="370"
                          :h="150"
                          ref="slideblock"
                          @again="onAgain"
                          @fulfilled="onFulfilled"
                          @success="onSuccess"
                          @fail="onFail"
                          :imgs="puzzleImgList"
                          @refresh="onRefresh"
                          :accuracy="accuracy"
                          :slider-text="text"
                        ></slide-verify>
                      </div>
                    </el-dialog>
             </div>
</template>

<script>
import aa from "../assets/images/bg1.png"  //  
export default {
  data() {
    return {
      dialogbuttonVisible: false,
      puzzleImgList: [
      ],
      text: "",
      // 15
      accuracy: 5,
    }
   },
  methods: {
    //
    verificationButton() {
      this.dialogbuttonVisible = true;
    },
    // 
    onSuccess(times) {
      console.log("");
      let ms = (times / 1000).toFixed(1);
      this.$message.success("login success,  " + ms + "s");
      setTimeout(()=> {
        this.dialogbuttonVisible = false;
        this.$router.push({name: 'Collapse'})
      }, 1000)
    },
    onFail() {
      console.log("");
      this.$message.error("");
    },
    onRefresh() {
      console.log("");
      this.$message.success();
    },
    onFulfilled() {
      console.log("");
    },
    onAgain() {
      console.log("");
      this.$message.error("try again");
      // 
      this.$refs.slideblock.reset();
    },
  }
}
</script>


