import axios from "axios";
import { Message } from "element-ui";
import router from "./router";
// axios
const instance = axios.create({ timeout: 10000 * 12 });
instance.interceptors.request.use(
    config => {
        console.log('')

        console.log("config.headers:", config.headers)
        let token = sessionStorage.getItem('token')
        let username = sessionStorage.getItem('username')
            // usernameetRequestHeader' on 'XMLHttpRequest': String contains non ISO-8859-1 code point.
        username = encodeURIComponent(username)
        config.headers.token = token
        config.headers.username = username

        return config

    },

    error => {
        console.log('')
        console.log(error)
        return Promise.reject(error)

    })

//token
instance.interceptors.response.use(
    response => {
        console.log("response interceptors process...", response);
        if (response.data.code === 10100) {
            sessionStorage.removeItem('username');
            sessionStorage.removeItem('token');
            Message.error("");
            setTimeout(() => {
                router.push({ path: "/" });
            }, 2000)

        }
        // console.log("overdue");
        return response;
    },
    error => {
        console.log("response interceptors process... error", error);
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    sessionStorage.removeItem('token');
                    // this.$router.push({ path: "/" });
            }
        }
    }
)
export default instance;


