<script>
export default {
  name: "AddZip",
  data() {
    return {
      form2: {
        importType: "increment",
        fileList: [],
        filebase64: "",
        isAgree: false,
      },
      dialogVisible: false,
      importContent: "",
      isImportContentDisable: false,
      // fullscreanLoading: false,
      rules2: {
        fileList: [{ required: true, message: "", trigger: "blur" }],
      },
      action: "/handler",
      // centerDialogVisible: false,
    };
  },
  created() {
    // let _this = this;
    // axios
    //   .get("http://localhost:8181/productCategory/init")
    //   .then(function (resp) {
    //     _this.options1 = resp.data;
    //   });
  },
  methods: {
    // openFullScreen1() {
    //   this.fullscreenLoading = true;
    //   setTimeout(() => {
    //     this.fullscreenLoading = false;
    //   }, 2000);
    // },
    showDialog() {
      this.dialogVisible = true;
    },
    handleClose(done) {
      this.$confirm("")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    open(response) {
      this.$alert(":" + response, "", {
        confirmButtonText: "",
        //   callback: action => {
        //     this.$message({
        //       type: 'info',
        //       message: `action: ${ action }`
        //     });
        //   }
      });
      this.$router.push({ name: "", params: { responses: response } });
    },
    //onChange
    onChange(file, fileList) {
      //
      this.$refs.form2.clearValidate("fileList");
      if (fileList.length > 0) {
        this.form2.fileList = [fileList[fileList.length - 1]]; //  
      }
      // var sizeTotal = 0;
      // console.log("fileList.length=>", fileList.length);
      // if (fileList.length > 10) {
      //   this.$message({
      //     message: " 10!",
      //     type: "warning",
      //   });
      //   fileList.splice(fileList.length - 1, 1);
      // }
      // for (var i = 0; i < fileList.length; i ++) {
      //   // sizeTotal += fileList[i].size;
      //   // console.log("f=>", fileList[i].size);
      //   if (fileList[i].size / 1024 / 1024 > 30) {
      //     this.$message({
      //       message: " 30MB!",
      //       type: "warning",
      //     });
      //     fileList.splice(fileList.length - 1, 1);
      //   }
      // }
      // console.log("sizeTotal=>", sizeTotal);
      // var isLt2M = sizeTotal / 1024 / 1024 < 10;
      const isLt2M = file.size / 1024 / 1024 < 30;
      if (!isLt2M) {
        this.$message({
          message: " 30MB!",
          type: "warning",
        });
        this.form2.fileList = [];
        return false;
      }
      // for (var i = 0; i < fileList.length; i ++) {
      //   this.form2.fileList = fileList;
      // }
      return isLt2M;
    },
    onSuccess(response) {
      //
      console.log("onSuccess:", response);
      if (response) {
        if (response.data.code === 10000) {
          // if(response.state==1){
          this.$message.success("");
          this.dialogVisible2 = false;
          // this.open(response);
          // this.$store.commit('setCollapse', {data: response});
          sessionStorage.setItem("collapsedata", response.data.msg);
          sessionStorage.setItem("pic", response.data.pic);
          var dataStr = JSON.stringify(response.data);
          console.log("dataStr=>", dataStr);
          // console.log("dataStr[0]=>", dataStr[0]);
          sessionStorage.setItem("scoredata", dataStr);
          this.$router.push({
            name: "",
            params: { responses: response },
          });
        } else if (response.data.code === 11100) {
          this.$message.error("");
        } else if (response.data.code === 11111) {
          this.$message.error("");
        }
      }

      this.form2.fileList = [];
      this.$refs["form2"].resetFields();
      this.$refs["newupload"].clearFiles();
      this.importContent = "";
      this.isImportContentDisable = false;
    },
    axiosMethod() {
      this.$axios.post("/tokenAvailable").then((resp) => {
        console.log("tokenAvailable:", resp);
      });
    },
    importFilepost() {
      //---
      // let _this = this;
      // const form = new FormData();
      // form.append('beer', params.file);
      // this.$refs.newupload.submit(); 
      // this.axios.post("http://127.0.0.1:5000/handler", form).then(
      //                 res => {
      //                     this.result = res.data
      //                     console.log(res.data)
      //                 }
      //             ).catch(res => {
      //                 console.log("error")
      //             })
      // if (this.centerDialogVisible) {
      //   this.centerDialogVisible = false;
      // }

      // this.$axios.post("/handler", formData).then( resp => {
      //   console.log("formData resp:", resp)
      // });
      if (this.form2.isAgree === false) {
        this.$message.error("");
      } else {
        this.$refs.form2.validate((valid) => {
          if (valid) {
            let files = [];
            let formData = new FormData();
            //  this.$refs.newupload.submit() bug
            for (var i = 0; i < this.form2.fileList.length; i ++) {
              let file = this.$refs.newupload.uploadFiles.pop().raw;
              // files.push(file);
              formData.append('files', file);
            }
            // let file = this.$refs.newupload.uploadFiles.pop().raw;
            // let formData = new FormData();

            // formData.append("file", file);
            console.log("valid:", valid);
            if (this.form2.isAgree === false) {
              this.$message.error("");
              // debugger;
            } else {
              // setTimeout(() => {}, 5000);
              this.$message.success("....");
              this.importContent = "";
              this.isImportContentDisable = true;
              this.$axios.post("/handler", formData).then((resp) => {
                // if (resp.data.code === 10000) {
                //   //action
                //   // this.$refs.newupload.submit(); //
                //   this.onSuccess(resp);
                // }

                this.onSuccess(resp);
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    onCancel() {
      //
      this.$refs.form2.resetFields();
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.fileList = [];
    },
  },
};
</script>

