import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import ProductManage from "../views/ProductManage";
import Bar from "../views/Bar";
import Pie from "../views/Pie";
import AddZip from "../views/AddZip"
import Collapse from "../views/Collapse"
import Memu from "../views/Memu"
import Login from "../views/Login"
import Register from "../views/Register"
import Homepage from "../views/Homepage"
import SlideCaptcha from "../views/SlideCaptcha"
import Welcome from "../views/Welcome"
import Forget from "../views/Forget"
import MutilAdd from "../views/MutilAdd"
import History from "../views/History"




Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home,
        redirect: '/welcome',
        children: [{
                path: '/productManage',
                name: '',
                component: ProductManage
            },
            {
                path: '/welcome',
                name: '',
                component: Welcome
            },
            {
                path: '/addZip',
                name: '',
                component: AddZip
            },
            {
                path: '/mutilAdd',
                name: '',
                component: MutilAdd
            },
            {
                path: '/bar',
                name: '',
                component: Bar
            },
            {
                path: '/pie',
                name: '',
                component: Pie
            },
            {
                path: '/collapse',
                name: '',
                component: Collapse
            },
            {
                path: '/memu',
                component: Memu
            },
            {
                path: '/history',
                component: History
            }
        ]
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/register',
        name: 'Register',
        component: Register
    },
    {
        path: '/homepage',
        name: 'Homepage',
        component: Homepage
    },
    {
        path: '/captcha',
        name: 'SlideCaptcha',
        component: SlideCaptcha
    },
    {
        path: '/forget',
        name: 'ForgetPass',
        component: Forget
    }
]



const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})





router.beforeEach((to, from, next) => {
    // 
    // 
    let token = sessionStorage.getItem('token')
        // let userName = sessionStorage.getItem('user')
    if (to.path === '/register' | to.path === '/forget') {
        next()
    } else if (to.path === '/login') {
        // 
        if (token) {
            next({ path: '/' })
        } else {
            next()
        }
    } else {
        if (!token) {
            // 
            next({ path: '/login' })
        } else {
            // 
            // addDynamicMenuAndRoutes(userName, to, from)
            next()
        }
    }
})

export default router



